<template>
  <div>

    <h1 class="pb-2">
     
        Los Cuentos más populares.
     </h1>  
      <h3 class="texttema">
          Los proyectos , ideas, histórias más populares que podrás encontrar, disfrutar y aprender.<nav></nav> <br>
     </h3>
      


      <div class="container-md">
          <b-card-group columns>
            <div columns v-for="proyecto in proyectos" :key="proyecto.id">
             <Card2 :autor="proyecto.autor" :srcimg="proyecto.srcimg" :alt="proyecto.alt" :titulo="proyecto.titulo" :texto="proyecto.texto" :url="proyecto.url"></Card2> 
            </div>
           </b-card-group>
       </div>
 </div>

</template>

<script>
import Card2 from '../components/Card2.vue' 

const lstProysJson = '../cardautors.json';

export default {
  props: {  
   name: {
      type: String,
      default: 'ListaProyectos'
    }
  },
  components: {
        Card2
      },
  data() {
      return {
        proyectos:[],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 5
      }
    },
    mounted() {
      this.fetchData()
    },
 
    methods: {
        async fetchData() {
          const res = await fetch (lstProysJson);
          const val= await res.json();
          this.proyectos=val;
          console.log(val);
        }
    }
  }
</script>


<style>
.texttema{
    font-size: 20px;
    max-width: 50rem;
    display: inline-block;
}
</style>
